<!-- eslint-disable vue/no-v-html -->
<template>
  <div>
    <v-list class="chat-list">
      <v-list-item v-for="(msg, i) in currentChat.messages" :key="i" class="my-0">
        <v-avatar v-if="msg.role !== 'user'" color="warning" size="30">
          <v-icon size="20" dark>mdi-robot</v-icon>
        </v-avatar>
        <v-avatar v-else color="grey" size="30">
          <v-icon size="20" dark>mdi-account</v-icon>
        </v-avatar>
        <div :class="msg.role !== 'user' ? 'blue white--text pa-2 ml-3' : 'ml-1'" class="px-2 rounded-lg text-body-2 text-pre-wrap">
          {{ msg.content }}
        </div>
      </v-list-item>
      <!-- loading icon -->
      <v-list-item v-if="sendingMessage">
        <v-avatar color="warning" size="30">
          <v-icon size="20" dark>mdi-robot</v-icon>
        </v-avatar>
        <BeatLoader color="blue" size="12px" class="ml-3"></BeatLoader>
      </v-list-item>
    </v-list>
    <!-- CHAT PROMPT -->
    <div v-if="error" class="error--text text-right caption">
      {{ error }}
    </div>
    <div class="d-flex" :class="$vuetify.breakpoint.smAndDown ? 'sm-prompt-field' : 'lg-prompt-field'">
      <v-textarea v-model="message" block placeholder="Your prompt here" outlined dense hide-details rows="1" auto-grow></v-textarea>
      <v-btn color="primary" class="px-8 ml-2" @click="sendMessage">Send</v-btn>
    </div>
  </div>
</template>

<script>
import BeatLoader from "vue-spinner/src/BeatLoader";
import { mapActions, mapGetters, mapMutations } from "vuex";

export default {
  name: "ChatList",
  components: { BeatLoader },
  props: {},
  data: () => ({
    message: "",
    error: "",
    sendingMessage: false,
  }),
  computed: {
    ...mapGetters(["currentChat"]),
  },
  created() {},
  methods: {
    ...mapMutations(["setCurrentChatId", "addMessageToChat", "addNewChat"]),
    ...mapActions(["submitPrompt"]),
    async sendMessage() {
      try {
        this.error = "";
        // create a chat if it doesn't exist
        if (!this.currentChat.id) {
          const newId = crypto.randomUUID();
          this.addNewChat({ id: newId, title: "Chat with ChatGPT", question: this.message });
          this.setCurrentChatId(newId);
        }
        this.addMessageToChat({ chatId: this.currentChat.id, message: { role: "user", content: this.message } });
        this.message = "";
        this.$vuetify.goTo(document.body.scrollHeight);

        // send prompt to server
        this.sendingMessage = true;
        const response = await this.submitPrompt({ messages: this.currentChat.messages });
        this.addMessageToChat({ chatId: this.currentChat.id, message: this.parseResponse(response) });
      } catch (e) {
        console.log(e);
        this.error = e.message;
      } finally {
        this.sendingMessage = false;
      }
    },
    parseResponse(response) {
      const { content } = response;
      // split by \n\n
      const paragraphs = content.split("\n\n");
      const newContent = paragraphs.join("\n\n");
      return { ...response, content: newContent };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "ChatContent.scss";
</style>
