<template>
  <div>
    <div class="d-flex px-2 py-3">
      <v-btn color="grey" class="px-8 flex-grow-1" @click="createNewChat">New Chat</v-btn>
      <v-btn color="grey" class="ml-2"><v-icon size="26">mdi-dots-vertical</v-icon></v-btn>
    </div>
    <div class="px-2">
      <v-text-field v-model="search" block placeholder="Search chats" outlined dense hide-details rows="1" auto-grow></v-text-field>
    </div>
    <v-list class="mt-4">
      <v-list-item-group v-model="selectedChatId">
        <v-list-item v-for="chat in chats" :key="chat.id" link @click="saveCurrentChat(chat.id)">
          <v-list-item-icon>
            <v-icon>mdi-forum-outline</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ chat.title }}</v-list-item-title>
            <v-list-item-subtitle>{{ chat.question }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "ChatList",
  props: {},
  data: () => ({
    selectedChatId: null,
    search: "",
  }),
  computed: {
    ...mapGetters(["currentChatId", "currentChat", "chatList"]),
    chats() {
      return Object.values(this.chatList).filter((chat) => chat.title.toLowerCase().includes(this.search.toLowerCase()));
    },
  },
  watch: {
    selectedChatId() {
      if (this.selectedChatId == null) this.setCurrentChatId(null);
    },
    currentChatId() {
      if (this.currentChatId == null) this.selectedChatId = null;
    },
  },
  created() {
    this.selectedChatId = this.currentChat.id ? this.chats.findIndex((chat) => chat.id === this.currentChat.id) : null;
  },
  methods: {
    ...mapMutations(["setCurrentChatId"]),
    saveCurrentChat(chatId) {
      this.setCurrentChatId(chatId);
    },
    createNewChat() {
      this.selectedChat = null;
      this.setCurrentChatId(null);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "ChatList.scss";
</style>
